    height: 34px;
    line-height: 34px;
    font-size: 14px;
    color: #454545;
    padding-left: 6px;
    border: none;
    background: none;
  }
  .search-submit {
    width: 38px;
    height: 34px;
    line-height: 34px;
    text-indent: 999em;
    border: none;
    background: none;
  }
  .FL {
    float: left;
    display: inline;
    overflow: hidden;
  }
</style>



