const Index = () => import(/* webpackChunkName: "group-home" */ '@/views/index')

const router = {
  path:'/',
  name:'Index',
  component:Index,
  children: [
    {
      path: '/',
      name: 'Index',
      component: Index
    }
  ]
}

export default router




